import React, { useState } from 'react';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem,
    UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem,
    Modal, ModalBody, ModalHeader, Row, Col, Button, Label,
    Toast, ToastHeader } from 'reactstrap';
import { NavLink } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import logo from '../images/wkLogoTransparent.png';

const required = (val) => val && val.length;

function HomeHeader (props) {
    const [navIsOpen, setNavIsOpen] = useState(false);
    const toggleNav = () => setNavIsOpen(!navIsOpen);

    const [modalIsOpen, setModalIsOpen] = useState(false);
    const toggleModal = () => setModalIsOpen(!modalIsOpen);

    function UserButton (signedIn) {
        if (signedIn.signedIn !== null) {
            return (
                <Nav className="ml-auto" navbar>
                    <NavItem>
                        <UncontrolledDropdown nav inNavbar className="ml-auto">
                            <DropdownToggle nav caret>
                            <span className="fa fa-user-circle-o" /> {signedIn.signedIn.name}
                            </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem onClick={toggleModal}>
                                    <span className="fa fa-cog" /> Profile Settings
                                </DropdownItem>
                                <DropdownItem onClick={() => {props.signout(); window.localStorage.clear()}}>
                                    <span className="fa fa-sign-out" /> Sign Out
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </NavItem>
                </Nav>  
            );
        }
        else {
            return (
                <></>
            );
        }
    }

    const [showFail, setShowFail] = useState(false);
    const toggleSetShowFail = () => setShowFail(!showFail);

    const [showSuccess, setShowSuccess] = useState(false);
    const toggleSetShowSuccess = () => setShowSuccess(!showSuccess);

    const handleChangePassword = (values) => {
        if (props.dashboard.signedIn.password === values.oldPassword) {
            props.changePassword (props.dashboard.signedIn.id, props.dashboard.signedIn.name, props.dashboard.signedIn.email, values.newPassword);
            toggleSetShowSuccess();
        }
        else {
            toggleSetShowFail();
        }
    }

    const toggleDeleteAccount = () => {
        var sure = window.confirm("Are you sure you want to delete you account? All your saved information will be permanently deleted.");
        if (sure) {
            const userWarranties = props.warranties.warranties.filter ((warranty) => warranty.userId === props.dashboard.signedIn.id);
            for (var i = 0; i < userWarranties.length; i++) {
                props.deleteWarranty (userWarranties[i].id);
            }
            props.deleteAccount (props.dashboard.signedIn.id);
            props.signout();
            toggleModal();
        }
    }

    const href = (props.dashboard.signedOut) ? '/wk/home' : '/wk/dashboard';

    return (
        <>
            <header>
                <Navbar dark expand="md">
                    <div className="container">
                    <NavbarBrand href={href} className="mr-auto">
                        <img src={logo} alt="Logo" height="25px" width="auto" />
                    </NavbarBrand>
                    <NavbarToggler onClick={toggleNav} />
                        <Collapse isOpen={navIsOpen} navbar>
                            <Nav className="mr-auto" navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/wk/home">
                                        <span className="fa fa-home"> Home</span>
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/wk/contact">
                                        <span className="fa fa-address-card"> Contact Us</span>
                                    </NavLink>
                                </NavItem>
                            </Nav>
                            <UserButton signedIn={props.dashboard.signedIn} />
                        </Collapse>
                    </div>
                </Navbar>
            </header>

            <Modal isOpen={modalIsOpen} toggle={toggleModal}>
                <ModalHeader toggle={toggleModal}>Profile Settings</ModalHeader>
                <ModalBody>
                    <h6>Change Password</h6>
                    <LocalForm onSubmit={(values) => handleChangePassword (values)} >
                        <Row className="form-group">
                            <Col xs={12}>
                                <Toast isOpen={showFail} style={{backgroundColor: "#ff0000"}}>
                                    <ToastHeader toggle={() => toggleSetShowFail()}>
                                        <span className="fa fa-exclamation-triangle" /> Incorrect old password.
                                    </ToastHeader>
                                </Toast>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Col xs={12}>
                                <Toast isOpen={showSuccess} style={{backgroundColor: "#00ff00"}}>
                                    <ToastHeader toggle={() => { toggleSetShowSuccess(); toggleModal() }}>
                                        <span className="fa fa-check-circle" /> Password changed successfully.
                                    </ToastHeader>
                                </Toast>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="oldPassword" md={4}>Old Password</Label>
                            <Col md={8}>
                                <Control.text type="password" model=".oldPassword" id="oldPassword" name="oldPassword"
                                    placeholder="Old Password" className="form-control"
                                    validators={{ required }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".oldPassword" show="touched"
                                    messages={{ required: ' Required.' }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="newPassword" md={4}>New Password</Label>
                            <Col md={8}>
                                <Control.text type="password" model=".newPassword" id="newPassword" name="newPassword"
                                    placeholder="New Password" className="form-control"
                                    validators={{ required }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".newPassword" show="touched"
                                    messages={{ required: ' Required.' }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Col xs={12}>
                                <Button type="submit" color="primary">
                                    <span className="fa fa-key" /> Change Password
                                </Button>
                            </Col>
                        </Row>
                    </LocalForm>
                    <hr />
                    <h6>Delete Account</h6>
                    <Row>
                        <Col xs={12}>
                            <Button color="danger" onClick={() => toggleDeleteAccount()}><span className="fa fa-ban" /> Delete Account</Button>
                        </Col>
                    </Row>
                </ModalBody>
            </Modal>
        </>
    );
}

export default HomeHeader;