import React, { useState } from 'react';
import { Button, Card, CardBody, CardHeader,
    ListGroup, ListGroupItem, Fade,
    Table, Row, Col, Label, CardFooter } from 'reactstrap';
import { Control, LocalForm, Errors } from 'react-redux-form';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);
const isNumber = (val) => !isNaN(Number(val));
const validDate = (val) => /^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[13-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/.test(val);

function BlankCard (props) {
    if (props.show) {
        return (
            <Fade className="h-100 w-100">
                <Card className="h-100 w-100" id="blankCard">
                    <CardBody className="d-flex align-items-center justify-content-center">
                        <p><span className="fa fa-info-circle" /> Click on a warranty name to see details or add new warranty information</p>
                    </CardBody>
                </Card>
            </Fade>
        );
    }
    else {
        return (
            <></>
        );
    }  
}


function WarrantyDetail (warranty) {
    if (warranty.warranty !== null) {
        var invoiceDateParts = warranty.warranty.invoiceDate.split("/");
        var invoiceDateObject = new Date(+invoiceDateParts[2], invoiceDateParts[1] - 1, +invoiceDateParts[0]); 
        var invoiceDate = new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(invoiceDateObject);
        var warrantyPeriodObject = invoiceDateObject.setMonth( invoiceDateObject.getMonth() + parseInt (warranty.warranty.warrantyPeriod) );
        var warrantyUpto = new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(warrantyPeriodObject);

        return (
            <Fade className="h-100 w-100">
                <Card className="h-100 w-100">
                    <CardHeader>{warranty.warranty.name}</CardHeader>
                    <CardBody className="scrollable scrollbar-hidden">
                        <Table responsive>
                            <tbody>
                                <tr>
                                    <th scope="row">Product Name</th>
                                    <td>{warranty.warranty.name}</td>
                                </tr>
                                <tr>
                                    <th scope="row">Invoice Number</th>
                                    <td>{warranty.warranty.invoiceNum}</td>
                                </tr>
                                <tr>
                                    <th scope="row">Purchased on</th>
                                    <td>{invoiceDate}</td>
                                </tr>
                                <tr>
                                    <th scope="row">Purchased from</th>
                                    <td>{warranty.warranty.seller}</td>
                                </tr>
                                <tr>
                                    <th scope="row">Warranty Number</th>
                                    <td>{(warranty.warranty.warrantyNum) ? warranty.warranty.warrantyNum : "NA"}</td>
                                </tr>
                                <tr>
                                    <th scope="row">Covered upto</th>
                                    <td>{warrantyUpto}</td>
                                </tr>
                                <tr>
                                    <th scope="row">Comments</th>
                                    <td>{(warranty.warranty.comments) ? warranty.warranty.comments : "NA"}</td>
                                </tr>
                            </tbody>
                        </Table>
                    </CardBody>
                    <CardFooter>
                        <Row>
                            <Col xs={12}>
                                <Button outline color="danger" onClick={() => {
                                    var sure = window.confirm ("Are you sure you want to delete the warranty information?");
                                    if (sure) warranty.deleteWarranty (warranty.warranty.id)}} block>
                                    <span className="fa fa-trash" /> Delete
                                </Button>
                            </Col>
                        </Row>
                    </CardFooter>
                </Card>
            </Fade>
        );
    }
    else {
        return (
            <></>
        );
    } 
}

function Dashboard (props) {
    const [showDetail, setShowDetail] = useState(null);
    const toggleShowDetail = (warranty) => setShowDetail(warranty);
    const toggleShowDetailToNull = () => setShowDetail(null);

    const [addWarrantyInfo, setAddWarranty] = useState(false);
    const toggleAddWarranty = () => setAddWarranty(!addWarrantyInfo);
    const toggleAddWarrantyToFalse = () => setAddWarranty (false);

    if (props.dashboard.signedIn !== null)
    {
        const userId = props.dashboard.signedIn.id;
        const userWarranties = props.warranties.warranties.filter ((warranty) => warranty.userId === userId);
        const warrantyList = userWarranties.map ((warranty) => {
            return (
                <ListGroupItem key={warranty.id} tag="button" action
                    onClick={() => { toggleAddWarrantyToFalse(); toggleShowDetail(warranty); }}>
                        {warranty.name}
                </ListGroupItem>
            );
        });

        if (props.warranties.isLoading) {
            console.log ("Warranties Loading");
        }
        else if (props.warranties.errMess) {
            console.log ("Error:" + props.warranties.errMess);
        }

        return (
            <main className="container d-flex flex-row flex-grow-1">
                <div className="row flex-grow-1 scrollable scrollbar-hidden">
                    <div className="col-md-4 p-3">
                        <Card className="h-100 scrollable scrollbar-hidden">
                            <CardHeader>
                                Your Warranties
                            </CardHeader>
                            <CardBody>
                                <Button color="primary" block className="mb-3"
                                    onClick={() => { toggleShowDetailToNull(); toggleAddWarranty(); }}>
                                        <span className="fa fa-plus" /> Add new warranty information
                                </Button>
                                <Row className="justify-content-center">
                                    <span className={(!props.warranties.isLoading) ? "hideSpinner" : "fa fa-spinner fa-pulse fa-3x fa-fw fa-primary"} />
                                </Row>
                                <ListGroup>
                                    {warrantyList}
                                </ListGroup>
                            </CardBody>
                        </Card>
                    </div>
                    <div className="col-md-8 flex-column p-3">
                        <BlankCard show={!addWarrantyInfo && (showDetail === null)} />
                        <AddWarranty addWarranty={props.addWarranty} userId={props.dashboard.signedIn.id} show={addWarrantyInfo} />
                        <WarrantyDetail warranty={showDetail} deleteWarranty={props.deleteWarranty} />
                    </div>
                </div>
            </main> 
        );
    }
    else {
        props.history.push ('/wk/home');
        return (
            <></>
        );
    }
    
    function AddWarranty (props) {
        const handleAddWarranty = (values) => {
            const valuesWithUserId = { ...values, userId: props.userId }
            props.addWarranty(valuesWithUserId);
        }
        if (props.show === true) {
            return (
                <Fade className="h-100 w-100">
                <Card className="h-100 w-100">
                    <CardHeader>Add new warranty information</CardHeader>
                    <CardBody className="scrollable scrollbar-hidden">
                    <LocalForm onSubmit={(values) => handleAddWarranty (values)} id="addWarrantyForm">
                        <Row className="form-group">
                            <Label htmlFor="name" md={4}>Product Name</Label>
                            <Col md={8}>
                                <Control.text model=".name" id="name" name="name"
                                    placeholder="Product Name" className="form-control"
                                    validators={{ required, minLength: minLength(3), maxLength: maxLength(20) }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".name" show="touched"
                                    messages={{
                                        required: ' Required.',
                                        minLength: ' Product Name too short.',
                                        maxLength: ' Product Name too long.'
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="invoiceNum" md={4}>Invoice Number</Label>
                            <Col md={8}>
                                <Control.text model=".invoiceNum" id="invoiceNum" name="invoiceNum"
                                    placeholder="Invoice Number" className="form-control" 
                                    validators={{ required }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".invoiceNum" show="touched"
                                    messages={{ required: ' Required.' }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="invoiceDate" md={4}>Purchased on</Label>
                            <Col md={8}>
                                <Control type="date" model=".invoiceDate" id="invoiceDate" name="invoiceDate"
                                    placeholder="Invoice Date (DD/MM/YYYY)" className="form-control"
                                    validators={{ required, validDate }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".invoiceDate" show="touched"
                                    messages={{ required: ' Required.', validDate: ' Enter a valid date in the format DD/MM/YYYY' }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="seller" md={4}>Purchased from</Label>
                            <Col md={8}>
                                <Control.text model=".seller" id="seller" name="seller"
                                    placeholder="Seller" className="form-control"
                                    validators={{ required, minLength: minLength(3), maxLength: maxLength(15) }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".seller" show="touched"
                                    messages={{
                                        required: ' Required.',
                                        minLength: ' Seller Name too short.',
                                        maxLength: ' Seller Name too long.'
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="warrantyNum" md={4}>Warranty Number</Label>
                            <Col md={8}>
                                <Control.text model=".warrantyNum" id="warrantyNum" name="warrantyNum"
                                    placeholder="Warranty Number (if applicable)" className="form-control" 
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="warrantyPeriod" md={4}>Warranty Period</Label>
                            <Col md={8}>
                                <Control.text model=".warrantyPeriod" id="warrantyPeriod" name="warrantyPeriod"
                                    placeholder="Warranty Period (in months)" className="form-control"
                                    validators={{ required, isNumber }}
                                />
                                <Errors className="fa fa-exclamation-circle text-danger" model=".warrantyPeriod" show="touched"
                                    messages={{ required: ' Required.', isNumber: ' Enter number of months of warranty period.' }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="comments" md={4}>Comments</Label>
                            <Col md={8}>
                                <Control.textarea rows="2" model=".comments" id="comments" name="comments"
                                    placeholder="Additional Information" className="form-control" 
                                />
                            </Col>
                        </Row>
                    </LocalForm>
                    </CardBody>
                    <CardFooter>
                    <Row>
                            <Col md={6}>
                                <Button type="submit" outline color="primary" block form="addWarrantyForm">
                                    Add
                                </Button>
                            </Col>
                            <Col md={6}>
                                <Button outline color="danger" block onClick={() => toggleAddWarrantyToFalse()} >
                                    Cancel
                                </Button>
                            </Col>
                        </Row>
                    </CardFooter>
                </Card>
                </Fade>
            );
        }
        else {
            return (
                <></>
            );
        }
    }
}

export default Dashboard;