import React, { useState } from 'react';
import { Card, CardImg, CardBody, CardTitle, CardText,
    Label, Button, Row, Col,
    Modal, ModalHeader, ModalBody,
    Toast, ToastHeader } from 'reactstrap';
import { Control, LocalForm, Errors } from 'react-redux-form';
import logo from '../images/wkLogo.jpg';

var formValues = { email: '', password: '' };
var clickOnSignIn = false;
var toastVisible = false;

const required = (val) => val && val.length;
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

function Home (props) {
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const toggleModal = () => setModalIsOpen(!modalIsOpen);

    const [show, setShow] = useState(toastVisible);
    const toggleFalse = () => setShow(false);

    function makeToastVisibleTrue () {
        toastVisible = true;
    }

    function makeToastVisibleFalse () {
        toggleFalse();
        toastVisible = false;
    }

    function handleSignIn (values) {
        makeToastVisibleFalse ();
        clickOnSignIn = true;
        props.fetchUsers();
        formValues.email = values.email;
        formValues.password = values.password;
    }

    function handleSignUp (values) {
        toggleModal();
        props.signup (values);
    }

    if (props.users.isLoading) {
        console.log ("Loading");
        console.log ("Form Values " + formValues);
        
    }

    else {
        const validUser = props.users.users.filter ((user) => (user.email === formValues.email) && (user.password === formValues.password))[0];
        if (validUser) {
            clickOnSignIn = false;
            let valuesWithId = { ...formValues, id: validUser.id, name: validUser.name };
            props.signin (valuesWithId);
            props.history.push ('/wk/dashboard');
            props.fetchWarranties();
        }  
        else if (formValues.email !== '') {
            if (clickOnSignIn) {
                makeToastVisibleTrue();
                clickOnSignIn = false;
            }
        }
            
    }

    const loading = () => {
        if (props.dashboard.isLoading) {
            return (
                <div className="loadingOverlay">
                    <div className="loadingSpan">
                        <span className="fa fa-spinner fa-pulse fa-4x fa-black mb-2" /> Please Wait
                    </div>
                </div>
            );
        }
    }

    if (props.dashboard.signedIn === null) {
        return (
            <>
                {loading()}
                <main className="container d-flex flex-row flex-grow-1">
                    <div className="row flex-grow-1 align-items-center scrollable scrollbar-hidden">
                        <div className="col-md-6 mt-3">
                            <h1>WarrantyKeeper</h1>
                            <p id="headLine">Lost the warranty information again? Don't let that happen anymore.
                                Keep your warranties stored here, and access them anytime, anywhere.
                            </p>
                        </div>
                        <div className="col-lg-2 col-md-1"></div>
                        <div className="col-lg-4 col-md-5 mb-10">
                            <Card>
                                <CardImg top src={logo} width="auto" />
                                <CardBody>
                                    <CardTitle>
                                        <h3>Sign In <span className={(!props.users.isLoading) ? "" : "fa fa-spinner fa-pulse fa-fw fa-primary"} /></h3>
                                    </CardTitle>
                                    <LocalForm onSubmit={(values) => handleSignIn (values)}>
                                        <Row className="form-group">
                                            <Col xs={12}>
                                                <Toast isOpen={show} style={{backgroundColor: "#ff0000"}}>
                                                    <ToastHeader  toggle={() => makeToastVisibleFalse()}>
                                                        <span className="fa fa-exclamation-triangle" /> Incorrect email address or password.
                                                    </ToastHeader>
                                                </Toast>
                                            </Col>
                                        </Row>
                                        <Row className="form-group">
                                            <Col xs={12}>
                                                <Control.text model=".email" id="signInEmail" name="email"
                                                    placeholder="Email Address" className="form-control"
                                                    validators={{ required, validEmail }}
                                                />
                                                <Errors className="fa fa-exclamation-circle text-danger" model=".email" show="touched"
                                                    messages={{required: ' Required.', validEmail: ' Enter a valid email address.'}}
                                                />
                                            </Col>
                                        </Row>
                                        <Row className="form-group">
                                            <Col xs={12}>
                                                <Control.text type="password" model=".password" id="signInPasword" name="password"
                                                    placeholder="Password" className="form-control"
                                                    validators={{ required }}
                                                />
                                                <Errors className="fa fa-exclamation-circle text-danger" model=".password" show="touched"
                                                    messages={{ required: ' Required.' }}
                                                />
                                            </Col>
                                        </Row>
                                        <Row className="form-group">
                                            <Col xs={12}>
                                                <Button type="submit" color="primary" block>
                                                    <span className="fa fa-sign-in" /> Sign In
                                                </Button>
                                            </Col>
                                        </Row>
                                    </LocalForm>
                                    <CardText>
                                        Don't have an account? <Button color="link" onClick={toggleModal} className="m-0 p-0">Sign Up</Button>
                                    </CardText>
                                </CardBody>
                            </Card>
                        </div>
                    </div>
                </main>
    
                <Modal isOpen={modalIsOpen} toggle={toggleModal}>
                    <ModalHeader toggle={toggleModal}>Create new account</ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={(values) => handleSignUp (values)} >
                            <Row className="form-group">
                                <Label htmlFor="name" md={4}>Name</Label>
                                <Col md={8}>
                                    <Control.text model=".name" id="name" name="name"
                                        placeholder="Name" className="form-control"
                                        validators={{ required }}
                                    />
                                    <Errors className="fa fa-exclamation-circle text-danger" model=".name" show="touched"
                                        messages={{ required: ' Required.' }}
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="email" md={4}>Email Address</Label>
                                <Col md={8}>
                                    <Control.text model=".email" id="email" name="email"
                                        placeholder="Email Address" className="form-control"
                                        validators={{ required, validEmail }}
                                    />
                                    <Errors className="fa fa-exclamation-circle text-danger" model=".email" show="touched"
                                        messages={{ required: ' Required.', validEmail: ' Enter a valid email address.'}}
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="password" md={4}>Password</Label>
                                <Col md={8}>
                                    <Control type="password" model=".password" id="password" name="password"
                                        placeholder="Password" className="form-control"
                                        validators={{ required }}
                                    />
                                    <Errors className="fa fa-exclamation-circle text-danger" model=".password" show="touched"
                                        messages={{ required: ' Required.' }}
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col xs={12}>
                                    <Button type="submit" color="primary">
                                        <span className="fa fa-user-plus" /> Create Account
                                    </Button>
                                </Col>
                            </Row>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </>
        );
    }
    else {
        props.history.push ('/wk/dashboard');
        return (
            <></>
        );
    }
    
}

export default Home;