import * as ActionTypes from './ActionTypes';

export const Warranties = (state = { isLoading: false, errMess: null, warranties: [] }, action) => {
    switch (action.type) {
        case ActionTypes.WARRANTIES_LOADING:
            return { ...state, isLoading: true, errMess: null, warranties: [] };

        case ActionTypes.LOAD_WARRANTIES:
            return { ...state, isLoading: false, errMess: null, warranties: action.payload };

        case ActionTypes.WARRANTIES_FAILED:
            return { ...state, isLoading: false, errMess: action.payload, warranties: [] };

        case ActionTypes.SIGN_OUT:
            return { ...state, isLoading: false, errMess: null, warranties: [] };

        default:
            return state;
    }
}