import React, { Component } from 'react';
import { Button, Row, Col, Label } from 'reactstrap';
import { Control, Form, Errors } from 'react-redux-form';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

class Contact extends Component {
    constructor (props) {
        super (props);

        this.handleSubmit = this.handleSubmit.bind (this);
    }

    handleSubmit(values) {
        this.props.resetFeedbackForm();
        this.props.postFeedback(values);
    }

    render() {
        return (
            <div className="container d-flex flex-column flex-grow-1">
                <div className="row flex-shrink-0">
                    <div className="col-12 mt-3">
                        <h1>Contact Us</h1>
                    </div>
                </div>
                <div className="row flex-grow-1 align-items-center scrollable scrollbar-hidden">
                    <div className="col-md-3">
                        <p>Tell us what you think, or directly connect with us on-</p>
                        <p style={{fontSize: '150%'}}>
                            <span className="fa fa-github">{' '}
                                <a href="https://github.com/ekagra25" target="_blank" rel="noopener noreferrer">ekagra25</a>
                            </span>
                        </p>
                        <p style={{fontSize: '150%'}}>
                            <span className="fa fa-linkedin-square">{' '}
                                <a href="https://www.linkedin.com/in/ekagra-midha/" target="_blank" rel="noopener noreferrer">Ekagra Midha</a>
                            </span>
                        </p>
                    </div>
                    <div className="col-md-9" style={{borderLeft: '2px solid #ffffff'}}>
                        <div className="container">
                            <Form model="feedback" onSubmit={(values) => this.handleSubmit(values)}>
                                <Row className="form-group ml-0">
                                    <h4>Tell us what you think...</h4>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="name" md={3}>Name</Label>
                                    <Col md={9}>
                                        <Control.text model=".name" id="name" name="name"
                                            placeholder="Name" className="form-control"
                                            validators={{ required }}
                                            />
                                        <Errors className="fa fa-exclamation-circle text-danger" model=".name" show="touched"
                                            messages={{ required: ' Required.' }}
                                        />
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="telnum" md={3}>Contact Number</Label>
                                    <Col md={9}>
                                        <Control.text model=".telnum" id="telnum" name="telnum"
                                            placeholder="Contact Number" className="form-control"
                                            validators={{ required, minLength: minLength(8), maxLength: maxLength(12), isNumber }}
                                        />
                                        <Errors className="fa fa-exclamation-circle text-danger" model=".telnum" show="touched"
                                            messages={{
                                                required: ' Required.',
                                                minLength: ' Contact Number too short.',
                                                maxLength: ' Contact Number too long.',
                                                isNumber: ' Please enter a valid contact number.'
                                            }}
                                        />
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="email" md={3}>Email</Label>
                                    <Col md={9}>
                                        <Control.text model=".email" id="email" name="email"
                                            placeholder="Email" className="form-control"
                                            validators={{ required, validEmail }}
                                        />
                                        <Errors className="fa fa-exclamation-circle text-danger" model=".email" show="touched"
                                            messages={{
                                                required: ' Required.',
                                                validEmail: ' Please enter a valid email address.'
                                            }}
                                        />
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Col md={{size: 9, offset: 3}}>
                                        <div className="form-check">
                                            <Label check>
                                                <Control.checkbox model=".agree" name="agree"
                                                    className="form-check-input"
                                                    /> {' '}
                                                    <strong>May we contact you?</strong>
                                            </Label>
                                        </div>
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="message" md={3}>Feedback</Label>
                                    <Col md={9}>
                                        <Control.textarea model=".message" id="message" name="message"
                                            rows="3" placeholder="Feedback"
                                            className="form-control" />
                                    </Col>
                                </Row>
                                <Row className="form-group mb-0">
                                    <Col md={{size:9, offset: 3}}>
                                        <Button type="submit" color="primary"> 
                                            Send Feedback
                                        </Button>
                                    </Col>
                                </Row>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;