import * as ActionTypes from './ActionTypes';
import { LOAD } from 'redux-storage'

export const Dashboard = (state = { signedIn: null, signedOut: true, stateLoaded: false }, action) => {
    switch (action.type) {
        case ActionTypes.SIGN_IN:
            return { ...state, signedIn: action.payload, signedOut: false };

        case ActionTypes.SIGN_OUT:
            return { ...state, signedIn: null, signedOut: true };

        case LOAD:
            return { ...state, stateLoaded: true };

        default:
            return state;
    }
}