import React, { Component } from 'react';
import Header from './HeaderComponent';
import Home from './HomeComponent';
import Dashboard from './DashboardComponent';
import Contact from './ContactComponent';
import Footer from './FooterComponent';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { actions } from 'react-redux-form';
import { signup, signin, fetchUsers, fetchWarranties, addWarranty, deleteWarranty, changePassword, deleteAccount, signout, postFeedback } from '../redux/ActionCreators';


const mapStateToProps = (state) => {
    return {
        users: state.users,
        dashboard: state.dashboard,
        warranties: state.warranties
    }
}

const mapDispatchToProps = (dispatch) => ({
    fetchUsers: () => { dispatch (fetchUsers()) },
    signin: (userDetails) => dispatch (signin (userDetails)),
    signout: () => dispatch (signout()),
    signup: (userDetails) => dispatch (signup (userDetails)),
    fetchWarranties: () => { dispatch (fetchWarranties()) },
    addWarranty: (warranty) => { dispatch (addWarranty (warranty)) },
    deleteWarranty: (warrantyId) => { dispatch (deleteWarranty (warrantyId)) },
    changePassword: (id, name, email, password) => { dispatch (changePassword (id, name, email, password)) },
    deleteAccount: (userId) => { dispatch (deleteAccount (userId)) },
    resetFeedbackForm: () => { dispatch (actions.reset ('feedback')) },
    postFeedback: (feedback) => { dispatch(postFeedback(feedback)) }
});

class Main extends Component {

    render() {
        const HomePage = ({history}) => {
            return (
                <Home
                    fetchUsers={this.props.fetchUsers}
                    fetchWarranties={this.props.fetchWarranties}
                    users={this.props.users}
                    signin={this.props.signin}
                    dashboard={this.props.dashboard}
                    signup={this.props.signup}
                    history={history}
                />
            );
        }

        const DashboardPage = ({history}) => {
            return (
                <Dashboard
                    fetchWarranties={this.props.fetchWarranties}
                    dashboard={this.props.dashboard}
                    warranties={this.props.warranties}
                    addWarranty={this.props.addWarranty}
                    deleteWarranty={this.props.deleteWarranty}
                    history={history}
                />
            );
        }

        if (this.props.dashboard.stateLoaded) {
            return (
                <>
                    <Header dashboard={this.props.dashboard}
                        signout={this.props.signout}
                        changePassword={this.props.changePassword}
                        deleteAccount={this.props.deleteAccount}
                        warranties={this.props.warranties}
                        deleteWarranty={this.props.deleteWarranty}
                    />
                    <Switch location={this.props.location}>
                        <Route exact path='/wk/home' component={ HomePage } />
                        <Route exact path='/wk/dashboard' component={ DashboardPage } />
                        <Route exact path='/wk/contact' component={() => <Contact postFeedback={this.props.postFeedback} resetFeedbackForm={this.props.resetFeedbackForm} />} />
                        {(this.props.dashboard.signedIn === null) ? <Redirect to='/wk/home' /> : <Redirect to='/wk/dashboard' />}
                    </Switch>
                    <Footer />
                </>
            );
        }
        else {
            return(<div className="loadingOverlay">
            <div className="loadingSpan">
                <span className="fa fa-spinner fa-pulse fa-4x fa-black mb-2" /> Please Wait
            </div>
        </div>);
        }
    }
}

export default withRouter (connect (mapStateToProps, mapDispatchToProps) (Main));