import * as ActionTypes from './ActionTypes';
import { baseURL } from '../shared/baseURL';

export const fetchUsers = () => (dispatch) => {
    dispatch (usersLoading (true));

    return fetch ('https://api.jsonbin.io/b/5f0fde73c58dc34bf5d4541c')
    .then (response => {
        if (response.ok) {
            return response;
        }
        else {
            var error = new Error (response.status + ': ' + response.statusText);
            error.message = response;
            throw error;
        }
    },
    error => {
        throw error;
    })
    .then (response => response.json())
    .then (users => dispatch (loadUsers (users)))
    .catch (error => { dispatch (usersFailed (error.message)); alert ("Error- " + error.message) });
}

export const usersLoading = () => ({
    type: ActionTypes.USERS_LOADING
})

export const loadUsers = (users) => ({
    type: ActionTypes.LOAD_USERS,
    payload: users
})

export const usersFailed = (errMess) => ({
    type: ActionTypes.USERS_FAILED,
    payload: errMess
})

export const signup = (userDetails) => (dispatch) => {

    const newUser = { ...userDetails };

    return fetch ('https://api.jsonbin.io/b/5f0fde73c58dc34bf5d4541c', {
        method: 'POST',
        body: JSON.stringify (newUser),
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'same-origin'
    })
    .then (response => {
            if (response.ok) {
                return response;
            }
            else {
                var error = new Error (response.status + ': ' + response.statusText);
                error.message = response;
                throw error;
            }
        },
        error => {
            throw error;
    })
    .then (response => response.json())
    .then (alert ("Your account has been created successfully."))
    .catch (error => {alert('Your account could not be created.\nError- ' + error.message)})
}

export const signin = (userDetails) => ({
    type: ActionTypes.SIGN_IN,
    payload: userDetails
})

export const signout = () => ({
    type: ActionTypes.SIGN_OUT
})

