export const changePassword = (id, name, email, newPassword) => (dispatch) => {
    return fetch (baseURL + 'users/' + id, {
        method: 'PUT',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify ({
            name: name,
            email: email,
            password: newPassword,
            id: id
        }),
        credentials: 'same-origin'
    })
    .then (response => {
            if (response.ok) {
                return response;
            }
            else {
                var error = new Error (response.status + ': ' + response.statusText);
                error.message = response;
                throw error;
            }
        },
        error => {
            throw error;
    })
    .then (response => response.json())
    .catch (error => {alert('Your request could not be completed.\nError- ' + error.message)})
}

